.dialogs {
    display: grid;
    grid-template-columns: 3fr 9fr;
}

.dialog a {
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
}
.dialogs__items .active {
    color: white;
}

.dialogs__items {
    color: #000;
}

.dialogs__items {

    padding: 5px;
}

.messages {
    padding: 5px;
}

