.app-wrapper{
  display: grid;
  width: 100%;
  margin: 0 auto;
  grid-template-areas: 
  'header header'
  'nav profile';
  grid-template-rows: 60px 1fr;
   grid-template-columns: 2fr 10fr;
   grid-gap: 10px;
}
.app-wrapper-content{
    grid-area: profile;
    background-color: rgb(0, 255, 196);
    padding: 5px;
}
