.nav {

    color: #0bac00;
    grid-area: nav;

    text-align: center;
}
.nav__areas{
    display: grid;
    grid-template-rows: 2fr 1fr;
    grid-gap: 5px;

}
.areas__item{
background-color: #ffaa00;
}
.item {
    text-transform: uppercase;
    text-decoration: none;
    padding: 0px 3px 3px 1px;
    color: #0037ff;
}

.item a {

    text-decoration: none;

    color: #0037ff;
}

.item a.active {
    padding: 0px 3px 3px 1px;

    text-decoration: none;
    font-size: 20px;
    color: green;
    border: 1px solid #0bac00;
    border-radius: 5px;
}

.areas__friends{
    background-color: #ffaa00;
}

.friends a {
    padding: 0px 3px 3px 1px;
    text-decoration: none;

    color: #0037ff;
    text-transform: uppercase;
    margin: 20px 20px 20px 20px;

}
.friends {
    padding: 0px 3px 3px 1px;
    text-decoration: none;

    color: #0037ff;
    text-transform: uppercase;
    margin: 20px 20px 20px 20px;

}
.friends a.active {
    padding: 0px 3px 0px 3px;
     margin: 20px 20px 20px 20px;
     text-decoration: none;
     /*font-size: 20px;*/
     color: green;
     border: 1px solid #0bac00;
     border-radius: 5px;
    text-transform: uppercase;
}
.active_name{
    margin: 5px 5px 5px 5px;
    /*padding: 5px;*/
    border: 1px solid #0bac00;
    border-radius: 5px;

}

.friend_name{
    margin: 20px;


}
.friend_name a{
    text-decoration: none;
    background-color: #0bac00;
    margin: 5px 5px 10px 5px;
    padding: 5px;
    border: 1px solid #0bac00;
    border-radius: 5px;
}